.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  padding-top: 220px;

  /* background-image: url('http://www.clker.com/cliparts/7/4/c/4/1446118397610284725jobs.png');
  background-position: left bottom;
  background-size: contain; */
}

.title {
  font-size: 65px;
  font-weight: 700;
  color: #497174;
  z-index: 3;
  background-color: #ffffff77;
  padding: 10px;
  border-radius: 5px;
  text-align: center;
  width: 100%;
}

.titleH2 {
  font-weight: 300;
  margin-bottom: 100px;
  z-index: 2;
  background-color: #ffffff77;
  padding: 10px;
  border-radius: 5px;
  width: 100%;
  text-align: center;
}

.divJobs {
  height: 100%;
  position: fixed;
  right: 0px;
  bottom: 0px;
}

.imgResponsive {
  max-height: 100%;
  width: auto;
  transform: rotateY(180deg);
  float: right;
  position: fixed;
  right: 0px;
  bottom: 0px;
  z-index: 1;
}

@media screen and (min-width: 1050px) {
  .imgResponsive {
    max-height: 50%;
  }
}
@media screen and (min-width: 1130px) {
  .imgResponsive {
    max-height: 55%;
  }
}
@media screen and (min-width: 1240px) {
  .imgResponsive {
    max-height: 60%;
  }
}
@media screen and (min-width: 1600px) {
  .imgResponsive {
    max-height: 80%;
  }
}

.btnHome {
  display: flex;
  justify-content: center;
  text-decoration: none;
  align-items: center;
  border: none;
  background-color: #497174;
  color: #ffffff;
  font-weight: 500;
  font-size: 23px;
  width: auto;
  padding: 0px 20px;
  z-index: 4;
  transition: all 300ms;
  border-radius: 10px;
  height: 90px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.btnHome:hover,
.btnHome:focus {
  background-color: #ffffff;
  color: #497174;
  animation-name: changeBgColor;
  animation-duration: 600ms;
  animation-timing-function: ease-in;
  animation-fill-mode: forwards;
  /* box-shadow: blue 0px 0px 0px 2px inset, rgb(255, 255, 255) 10px -10px 0px -3px,
    rgb(31, 193, 27) 10px -10px, rgb(255, 255, 255) 20px -20px 0px -3px,
    rgb(255, 217, 19) 20px -20px, rgb(255, 255, 255) 30px -30px 0px -3px,
    rgb(255, 156, 85) 30px -30px, rgb(255, 255, 255) 40px -40px 0px -3px,
    rgb(255, 85, 85) 40px -40px; */
}

@keyframes changeBgColor {
  0% {
  }
  20% {
    box-shadow: blue 0px 0px 0px 2px inset,
      rgb(255, 255, 255) 10px -10px 0px -3px;
  }
  40% {
    box-shadow: blue 0px 0px 0px 2px inset,
      rgb(255, 255, 255) 10px -10px 0px -3px, rgb(31, 193, 27) 10px -10px,
      rgb(255, 255, 255) 20px -20px 0px -3px;
  }

  60% {
    box-shadow: blue 0px 0px 0px 2px inset,
      rgb(255, 255, 255) 10px -10px 0px -3px, rgb(31, 193, 27) 10px -10px,
      rgb(255, 255, 255) 20px -20px 0px -3px, rgb(255, 217, 19) 20px -20px,
      rgb(255, 255, 255) 30px -30px 0px -3px;
  }
  80% {
    box-shadow: blue 0px 0px 0px 2px inset,
      rgb(255, 255, 255) 10px -10px 0px -3px, rgb(31, 193, 27) 10px -10px,
      rgb(255, 255, 255) 20px -20px 0px -3px, rgb(255, 217, 19) 20px -20px,
      rgb(255, 255, 255) 30px -30px 0px -3px, rgb(255, 156, 85) 30px -30px,
      rgb(255, 255, 255) 40px -40px 0px -3px;
  }

  100% {
    box-shadow: blue 0px 0px 0px 2px inset,
      rgb(255, 255, 255) 10px -10px 0px -3px, rgb(31, 193, 27) 10px -10px,
      rgb(255, 255, 255) 20px -20px 0px -3px, rgb(255, 217, 19) 20px -20px,
      rgb(255, 255, 255) 30px -30px 0px -3px, rgb(255, 156, 85) 30px -30px,
      rgb(255, 255, 255) 40px -40px 0px -3px, rgb(255, 85, 85) 40px -40px;
  }
}
